/**
 * The following styles get applied both on the front of your site
 * and in the editor.
 */

.wp-block-wallpaper-holiday-snow {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100%;
	min-height: inherit;
	overflow: hidden;
	overflow: clip;
	overflow-wrap: break-word;

	> * {
		margin-block: 0;
	}

	// Position: Top
	&.is-position-top-left {
		align-items: flex-start;
		justify-content: flex-start;
	}

	&.is-position-top-center {
		align-items: flex-start;
		justify-content: center;
	}

	&.is-position-top-right {
		align-items: flex-start;
		justify-content: flex-end;
	}

	// Position: Center
	&.is-position-center-left {
		align-items: center;
		justify-content: flex-start;
	}

	&.is-position-center-center {
		align-items: center;
		justify-content: center;
	}

	&.is-position-center-right {
		align-items: center;
		justify-content: flex-end;
	}

	// Position: Bottom
	&.is-position-bottom-left {
		align-items: flex-end;
		justify-content: flex-start;
	}

	&.is-position-bottom-center {
		align-items: flex-end;
		justify-content: center;
	}

	&.is-position-bottom-right {
		align-items: flex-end;
		justify-content: flex-end;
	}

	.wallpaper-image-background {
		position: absolute;
		inset: 0;
		margin: 0;
		padding: 0;
		width: 100%;
		height: 100%;
		max-width: none;
		max-height: none;
		object-fit: cover;
		outline: none;
		border: none;
		box-shadow: none;
	}

	.wallpaper-canvas {
		position: absolute;
		inset: 0;

		&.wallpaper-canvas {
			max-width: 100%;
		}

		.theme {
			position: relative;
			z-index: 2;
			width: 100%;
			height: 100%;
		}
	}

	.wallpaper-content {
		position: relative;
		z-index: 10;
	}

	.wallpaper-overlay {
		z-index: 1;

		&.has-background-dim {
			position: absolute;
			inset: 0;
			background-color: var(--wp--preset--color--black);

			@at-root .wp-block-post-featured-image {
				position: relative;
			}
		}

		&.has-background-gradient {
			background-color: transparent;
		}

		@for $i from 0 through 10 {
			&.has-background-dim-#{ $i * 10 } {
				opacity: $i * 0.1;
			}
		}
	}
}
